import { Component } from 'react';

import {Row, Col, Button} from 'reactstrap';

import firebase from 'firebase/app';
import 'firebase/database'; // If using Firebase database
import 'firebase/auth';

import dynamic from 'next/dynamic';
const CKEditor = dynamic(() => import('./CkeditorComp'), {
  ssr: false
})

class CKeditor extends Component {
  constructor(props){
    super(props);
    this.state={
      article: 'Makale'
    }
  }

  static getInitialProps({ req, query }) {
    return {}
  }

  updateMakale = (event, editor) => {
    const data = editor.getData();
    this.setState({
      article: data
    })
  }

  saveArticle = e => {
    const article = firebase.database().ref('articles');
    const mesaj = article.push({message: this.state.article, author: 'Berk Elmas'})
    this.setState({
      article : 'Makale Ekle'
    })

    e.preventDefault;
  }

  render() {
    return (
    <div>
    <Row className="mt-5">
      <Col sm="12" md={{ size: 8, offset: 2 }}>
      <h2 className="text-center">Makale Ekle</h2>
        <CKEditor
          data={this.state.article}
          onInit={ editor => {
          // You can store the "editor" and use when it is needed.
              console.log( 'Editor is ready to use!', editor );
          } }
          onChange={ ( event, editor ) => {
              this.updateMakale(event, editor)
          } }

          />
          <form onSubmit={this.saveArticle}>
            <Button className="btn btn-info mt-3 mb-5 w-100">Makale Ekle</Button>
          </form>
      </Col>
    </Row>
    </div>
    )
  }
}
export default CKeditor;
